	<div class="profile-content">
		<div class="profile-icon">
			<img :src="getImage(profileInfo.user.image_hash)">
		</div>
		<div class="profile-info">
			<p>{{profileInfo.user.name}}</p>
			<p class="mobile">
				<svg fill="#fff">
					<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#mobile"></use>
				</svg>
				<span>{{profileInfo.user.mobile}}</span>
			</p>
		</div>
		<div class="profile-arrow">
			<svg fill="#fff">
				<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
			</svg>
		</div>
	</div>
	<div class="maininfo">
		<div class="wallet">
			<p>
				<span>{{profileInfo.user.wallet}}</span>
				<em>元</em>
			</p>	
			<p>钱包</p>	
		</div>
		<div class="red-bag">
			<p>
				<span>{{profileInfo.user.redbag}}</span>
				<em>个</em>
			</p>	
			<p>红包</p>	
		</div>
		<div class="gold-coin">
			<p>
				<span>{{profileInfo.user.goldcoin}}</span>
				<em>个</em>
			</p>	
			<p>金币</p>	
		</div>
	</div>
	<section v-for="item in profileInfo.list" :key="item.id" :style="{marginTop:item.marginTop}">
		<div class="profile-item">
			<div class="item-icon">
				<svg fill="#4aa5f0"><use xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="item.icon"></use></svg>
			</div>
			<div class="item-name">
				<span>{{item.text}}</span>	
				<svg fill="#fff">
					<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
				</svg>
			</div>
		</div>	
	</section>
	<FooterMenu/>
  </div>
</template>
