.profile {
	#profile-balance .st0{fill-rule:evenodd;clip-rule:evenodd;fill:#f90}
	#crown .cls-1{mask:url(#mask);filter:url(#crown_luminosity-noclip-8)}#crown .cls-10,.cls-12,.cls-14,.cls-2,.cls-4,.cls-6,.cls-8{mix-blend-mode:multiply}#crown .cls-2{fill:url(#crown_未命名的渐变_10)}#crown .cls-3{mask:url(#mask-2);filter:url(#crown_luminosity-noclip-13)}#crown .cls-4{fill:url(#crown_未命名的渐变_10-2)}#crown .cls-5{mask:url(#mask-3);filter:url(#crown_luminosity-noclip-14)}#crown .cls-6{fill:url(#crown_未命名的渐变_10-3)}#crown .cls-7{mask:url(#mask-4);filter:url(#crown_luminosity-noclip-9)}#crown .cls-8{fill:url(#crown_未命名的渐变_10-4)}#crown .cls-9{mask:url(#mask-5);filter:url(#crown_luminosity-noclip-10)}#crown .cls-10{fill:url(#crown_未命名的渐变_10-5)}#crown .cls-11{mask:url(#mask-6);filter:url(#crown_luminosity-noclip-11)}#crown .cls-12{fill:url(#crown_未命名的渐变_13)}#crown .cls-13{mask:url(#mask-7);filter:url(#crown_luminosity-noclip-12)}#crown .cls-14{fill:url(#crown_未命名的渐变_10-6)}#crown .cls-15{fill:#dbc87b}#crown .cls-16{mask:url(#crown_mask-8)}#crown .cls-17{fill:url(#crown_未命名的渐变_6)}#crown .cls-18{mask:url(#crown_mask-9)}#crown .cls-19{fill:url(#crown_未命名的渐变_6-2)}#crown .cls-20{mask:url(#crown_mask-10)}#crown .cls-21{fill:url(#crown_未命名的渐变_6-3)}#crown .cls-22{opacity:.7}#crown .cls-23,.cls-25,.cls-26,.cls-31,.cls-33{opacity:.6}#crown .cls-23{fill:url(#crown_未命名的渐变_152)}#crown .cls-24{opacity:.4;fill:url(#crown_未命名的渐变_152-2)}#crown .cls-25{fill:url(#crown_未命名的渐变_152-3)}#crown .cls-26{fill:url(#crown_未命名的渐变_152-4)}#crown .cls-27{fill:#e8d997}#crown .cls-28{mask:url(#crown_mask-11)}#crown .cls-29{opacity:.8;fill:url(#crown_未命名的渐变_266)}#crown .cls-30{mask:url(#crown_mask-12)}#crown .cls-31{fill:url(#crown_未命名的渐变_6-4)}#crown .cls-32{mask:url(#crown_mask-13)}#crown .cls-33{fill:url(#crown_未命名的渐变_6-5)}#crown .cls-34{mask:url(#crown_mask-14)}#crown .cls-35{fill:url(#crown_未命名的渐变_266-2)}#crown .cls-36{filter:url(#crown_luminosity-noclip-7)}#crown .cls-37{filter:url(#crown_luminosity-noclip-6)}#crown .cls-38{filter:url(#crown_luminosity-noclip-5)}#crown .cls-39{filter:url(#crown_luminosity-noclip-4)}#crown .cls-40{filter:url(#crown_luminosity-noclip-3)}#crown .cls-41{filter:url(#crown_luminosity-noclip-2)}#crown .cls-42{filter:url(#crown_luminosity-noclip)}
	.profile-content {
		display:flex;
		padding:20px 15px;
		background-image:linear-gradient(90deg,#0af,#0085ff);
		align-items:center;
		.profile-icon {
			width:54px;
			height:54px;
			img {
				width:100%;
				height:100%;
				border-radius:50%;
			}
		}
		.profile-info {
			-webkit-flex-grow:1;
			flex-grow:1;
			margin-left:10px;
			font-size:18px;
			font-weight:700;
			font-family:PingFang SC,Helvetica Neue,Arial,sans-serif;
			color:#fff;
			p {
				svg {
					width:8px;
					height:10px;
				}
			}
			.mobile {
				margin-top:5px;
				font-size:12px;
			}
		}
		.profile-arrow {
			svg {
				width:10px;
				height:10px;
			}
		}
	}
	.maininfo {
		width:100%;
		height:74px;
		display:table;
		background-color:#fff;
		div {
			width:calc(100% / 3);
			display:table-cell;
			text-align:center;
			vertical-align:middle;
			p {
				color:#666;
				font-weight:700;
				font-size:11px;
				span {
					font-family:PingFang SC,Helvetica Neue,Arial,sans-serif;
					font-size:22px;
				}
				em {
					font-style:normal;
				}
			}
		}
		.wallet {
			span {
				color:rgb(0, 152, 251);
			}
			em {
				color:rgb(0, 152, 251);
			}
		}
		.red-bag {
			span {
				color:rgb(255, 95, 62);
			}
			em {
				color:rgb(255, 95, 62);
			}
		}
		.gold-coin {
			span {
				color:rgb(106, 194, 11);
			}
			em {
				color:rgb(106, 194, 11)
			}
		}
	}
	section {
		background-color:#fff;
		padding:0 10px;
		.profile-item {
			display:flex;
			padding:10px 0;
			-webkit-align-items:center;
			align-items:center;
			font-size:14px;
			.item-icon {
				margin-right:8px;
				svg {
					width:16px;
					height:16px;
				}
			}
			.item-name {
				display:flex;
				align-items:center;
				-webkit-justify-content:space-between;
				justify-content:space-between;
				flex:1;
				svg {
					width:10px;
					height:10px;
					fill:#bbb;
				}
			}
		}
	}
}

</style>



